<template>
  <div class="center-in-center" style="right: 4px;left: 4px;">
    <span style="font-family: '微软雅黑'; font-size: 12px;float: left;">
      <i class="el-icon-info"></i>这是你第一次使用，请根据提示输入你的<font color="red">昵称 </font>
    </span>
    <el-input
      placeholder="请输入你的昵称"
      v-model="nickname"
      clearable>
    </el-input>
    <div style="margin-top: 20px">
      <el-button type="primary" @click="regist">确定</el-button>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data() {
      return {
        nickname: '',
        openid: ''
      }
    },
    methods: {
      regist: function(){
        axios.get('/wechat_meilian/regist', {
          params: {
            openid: this.openid,
            nickname: this.nickname
          }
        }).then(response=>{
          let type = 'success';
          if(response.data.code == '500'){
            type = 'error';
          }
          this.$message({
            message: response.data.message,
            type: type,
            duration: 2000
          });
        })

      }
    },
    created: function(){
      this.openid = this.$route.query.openid
    }
  }
</script>

<style scoped>
  .center-in-center{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
</style>



