import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Login from '@/components/Login'
import Suggest from '@/components/Suggest'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      // redirect:'/login',
      children: [
        {
          path: '/login',
          component: Login,
          name: Login
        },{
          path: '/suggest',
          component: Suggest,
          name: Suggest
        }
      ]
    }
  ]
})



