<template>
  <div class="suggestion">
    <el-container>
      <el-header>
        <h2>意见提交</h2>
      </el-header>
      <el-main>
        <el-form label-position="top" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="70px" class="demo-ruleForm" size="medium">
          <el-form-item label="标题" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
          </el-form-item>

          <el-form-item label="内容" prop="desc">
            <el-input type="textarea" v-model="ruleForm.desc" :rows="5"></el-input>
          </el-form-item>
          <el-form-item class="button_align">
            <el-button type="primary" @click="submitForm('ruleForm')">立即提交</el-button>
            <el-button @click="resetForm('ruleForm')">重置所有</el-button>
          </el-form-item>
        </el-form>
      </el-main>
    </el-container>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: "Suggestion",
    data() {
      return {
        userid: '',

        ruleForm: {
          name: '',
          desc: ''
        },
        rules: {
          name: [
            { required: true, message: '请输入活动名称', trigger: 'blur' },
            { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          desc: [
            { required: true, message: '请填写活动形式', trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            //提交到后台入库
            axios({
              method: 'post',
              url: '/wechat_meilian/addSugg',
              data: {
                userid: this.userid,
                title: this.ruleForm.name,
                content: this.ruleForm.desc
              }
            }).then(response=>{
              let type = 'success';
              if(response.data.code == '500'){
                type = 'error';
              }
              this.$message({
                message: response.data.message,
                type: type,
                duration: 2000
              });
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    },
    created: function(){
      let query = this.$route.query;
      this.userid = query.userId;
    }
  }
</script>

<style scoped>
  .el-header {
    color: #333;
    text-align: center;
    line-height: 60px;
  }
  .el-main {
    color: #333;
    text-align: left;
  }
  .el-form-item__label{
    padding-bottom: 0!important;
  }
  .button_align{
    text-align: center!important;
  }
  .el-form-item__label{
    padding: 0px;
  }

</style>



